import {Figure, FigureNames} from "./Figure";
import {Colors} from "../Colors";
import {Cell} from "../Cell";
import blackLogo from "../../assets/black-pawn.png";
import whiteLogo from "../../assets/white-pawn.png";

export class Pawn extends Figure {
    direction: number = this.cell.figure?.color === Colors.BLACK ? 1 : -1;

    constructor(color: Colors, cell: Cell) {
        super(color, cell);
        this.logo = color === Colors.BLACK ? blackLogo : whiteLogo;
        this.name = FigureNames.PAWN;
    }

    canMove(target: Cell): boolean {
        if (!super.canMove(target))
            return false
        const firstStepDirection = this.cell.figure?.color === Colors.BLACK ? 2 : -2;

        if (target.y === (this.cell.y + this.direction)
            || (!this.hasBeenMoved && (target.y === this.cell.y + firstStepDirection))) {
            if (target.x === this.cell.x) {
                if (this.cell.board.getCell(target.x, target.y).figure === null) {
                    return true;
                }
            }
        }
        if (target.y === this.cell.y + this.direction) {
            if (target.x === this.cell.x + 1 || target.x === this.cell.x - 1) {
                if (this.cell.isEnemy(target)) {
                    return true
                }
            }
        }

        return false;
    }

    canAttack(target: Cell): boolean {
        if (target.y === this.cell.y + this.direction) {
            if (target.x === this.cell.x + 1 || target.x === this.cell.x - 1) {
                return true
            }
        }
        return false
    }
}